@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.corruption {
  &-page {
    padding-bottom: 130.8px;

    @include mixin.wide-tablet {
      padding-bottom: 80px;
    }

    @include mixin.small-tablet {
      padding-bottom: 64px;
    }
  }

  &__head {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &__list {
    margin-bottom: 48px;
  }
}

.corruption-contact {
  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 500;
    line-height: 1.66;
    margin-bottom: 8px;
  }

  &__text {
    color: var.$gray;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.66;
  }
}

.corruption-item {
  padding-bottom: 48px;
  margin-bottom: 48px;
  border-bottom: 1px solid #E4E4E4;

  @include mixin.wide-tablet {
    padding-bottom: 24px;
    margin-bottom: 24px;
  }

  &--active {
    @include mixin.wide-tablet {
      padding-bottom: 7px;
      border-color: transparent;
    }
  }

  &:last-child {
    padding-bottom: 0;
    margin-bottom: 100px;
    border: none;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64.59px;
    }
  }

  &__head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.66;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    transition: 0.3s linear;

    @include mixin.wide-tablet {
      font-size: 2.2rem;
    }

    @include mixin.small-tablet {
      font-size: 2rem;
      line-height: 1.6;
    }

    a {
      margin-top: 16px;
      color: var.$gray;
      font-family: var.$font-family;
      font-size: 2.2rem;
      font-weight: 300;
      line-height: 1.45;
      letter-spacing: 0.3px;
      text-decoration-line: underline;
      word-break: break-all;

      &:hover {
        color: var.$gray;
      }
    }
  }

  &:hover &__title {
    color: var.$primary;
    text-decoration-line: underline;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 100px;
    background: rgba(var.$secondary, 0.10);
    transition: 0.3s linear;
    transform-origin: center;

    svg {
      width: 24px;
      height: 24px;

      path {
        stroke: var.$secondary;
      }
    }
  }

  &:hover &__btn {
    background: rgba(var.$primary, 0.10);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--links &__btn {
    background: rgba(var.$primary, 0.10);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &__body {
    margin-top: 0;
    max-height: 0;
    overflow: hidden;
    display: flex;
    gap: 16px 0;
    transition: 0.3s linear;

    .col-6 {
      margin-bottom: 16px;
    }
  }

  &--active &__body {
    max-height: 600px;
    margin-top: 32px;

    @include mixin.wide-tablet {
      margin-top: 24px;
    }

    @include mixin.small-tablet {
      margin-top: 16px;
    }
  }

  &.transitionend &__body {
    overflow-y: auto;
  }
}